	module.exports = {"data":{"challengeNode":{"title":"Tokenize a string with escaping","description":["<p>","Write a function or program that can split a string at each non-escaped occurrence of a separator character.","</p>","<p>","It should accept three input parameters:","</p>","  The <b>string</b>","  The <b>separator character</b>","  The <b>escape character</b>","<p>It should output a list of strings.</p>","<p>Rules for splitting:</p>","  The fields that were separated by the separators, become the elements of the output list.","  Empty fields should be preserved, even at the start and end.","<p>Rules for escaping:</p>","  \"Escaped\" means preceded by an occurrence of the escape character that is not already escaped itself.","  When the escape character precedes a character that has no special meaning, it still counts as an escape (but does not do anything special).","  Each occurrences of the escape character that was used to escape something, should not become part of the output.","<p>Demonstrate that your function satisfies the following test-case:","  Given string <pre>one^|uno||three^^^^|four^^^|^cuatro|</pre> and using","  <pre>|</pre> as a separator and <pre>^</pre> as escape character, your","  function should output the following array:","</p>","  <pre>","  ['one|uno', '', 'three^^', 'four^|quatro', '']","  </pre>"],"challengeType":5,"fields":{"slug":"/coding-interview-prep/rosetta-code/tokenize-a-string-with-escaping","blockName":"Rosetta Code","tests":[{"text":"<code>tokenize</code> is a function.","testString":"assert(typeof tokenize === 'function', '<code>tokenize</code> is a function.');"},{"text":"<code>tokenize</code> should return an array.","testString":"assert(typeof tokenize('a', 'b', 'c') === 'object', '<code>tokenize</code> should return an array.');"},{"text":"<code>tokenize('one^|uno||three^^^^|four^^^|^cuatro|', '|', '^') </code> should return ['one|uno', '', 'three^^', 'four^|cuatro', '']\")","testString":"assert.deepEqual(tokenize(testStr1, '|', '^'), res1, \"<code>tokenize('one^|uno||three^^^^|four^^^|^cuatro|', '|', '^') </code> should return ['one|uno', '', 'three^^', 'four^|cuatro', '']\");"},{"text":"<code>tokenize('a@&bcd&ef&&@@hi', '&', '@')</code> should return <code>['a&bcd', 'ef', '', '@hi']</code>","testString":"assert.deepEqual(tokenize(testStr2, '&', '@'), res2, '<code>tokenize(\"a@&bcd&ef&&@@hi\", \"&\", \"@\")</code> should return <code>[\"a&bcd\", \"ef\", \"\", \"@hi\"]</code>');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function tokenize(str, esc, sep) {\n  return true;\n}","head":"","tail":"const testStr1 = 'one^|uno||three^^^^|four^^^|^cuatro|';\nconst res1 = ['one|uno', '', 'three^^', 'four^|cuatro', ''];\n\n// TODO add more tests\nconst testStr2 = 'a@&bcd&ef&&@@hi';\nconst res2 = ['a&bcd', 'ef', '', '@hi'];"},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/rosetta-code/topological-sort","id":"594faaab4e2a8626833e9c3d"},"slug":"/coding-interview-prep/rosetta-code/tokenize-a-string-with-escaping"}}

